import {useEffect, useState} from 'react';
import './App.css';
import Recipe from './Recipe'
require('dotenv').config()

function App() {

  const API_ID = process.env.REACT_APP_API_ID
  const API_KEY = process.env.REACT_APP_API_KEY

  const [recipe,setRecipe] = useState([])
  const [search,setSearch] = useState(' ')
  const [query,setQuery] = useState('korean fried chicken')
  
  useEffect(() => {
    console.log('effect has been run')

    const getRecipes = async () => {
      const res = await fetch(
        `https://api.edamam.com/search?q=${query}&app_id=${API_ID}&app_key=${API_KEY}`
        )
      const data = await res.json()
      setRecipe(data.hits)
      console.log(data.hits)
    };
    getRecipes()
  }, [query])

  const updateSearch = (e) => {
    setSearch(e.target.value)
    console.log(search)
  }

  const getSearch = (e) => {
    e.preventDefault()
    setQuery(search)
  }


  return (
    <div className="App">
      <div className='header'>
        <h1>Polaroid Recipe</h1>

        <form className='form'>
          <input className='search' type='text' placeholder='Search a recipe' onChange={updateSearch}/>
          <button type='submit' value='Submit' className='button' onClick={getSearch}>Submit</button>
        </form>
    </div>

      <div className='recipes'>
      {recipe.map((e) => (
        <Recipe
        key={e.recipe.url}
        title={e.recipe.label}
        calories={e.recipe.calories}
        image={e.recipe.image}
        serves={e.recipe.yield}
        recipeLink={e.recipe.url}
        time={e.recipe.totalTime}
        />
      ))}
      </div>

    </div>
  );
}

export default App;
