import style from './recipe.module.css'

const Recipe = ({title,calories,image,recipeLink,serves, time}) => {
    const cal = Math.round(calories/serves)

    if (time === 0){
        time = 'Click the recipe link'
    }
    return(
        <div className={style.recipeCard}>
            <a href={recipeLink} target="_blank" rel="noreferrer"> 
            <img src={image} alt={title}/>
        <div className={style.content}>
            <h2>{title}</h2>
            <p>Calories per serving: {cal}</p>
            <p>Time (mins): {time}</p>
            <p>Serves: {serves}</p>
        </div>
        </a>
        
        </div>
    )
}

export default Recipe

